.app-container{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack:justify;
      justify-content:space-between;
}

html, body, #root, .stretch-full {
  text-align: center;
  margin: 0;
  height: 100%;
}

.hug{
  width: auto;
  display: inline-block;
}

button {
  background-color: white;
  color: black;
  border: 1px solid black;
  cursor: pointer;
}


.editor {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 60%;
}

.center {
  -ms-flex-align:center;
      align-items:center;
  margin-left:auto;
  margin-right:auto;
}

.info span {
  font-size: 16pt;
}

.entries-container {
  width:60%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack:center;
      justify-content:center;
}

div.entry:hover {
  background-color: lightblue;
}

div.entry {
  cursor: pointer;
  -ms-flex-preferred-size: 20%;
      flex-basis: 20%;
  padding: 1em;
}

/* animations */
@-webkit-keyframes fade{
    0%{
        -webkit-transform: rotate(0) translateY(-15%);
                transform: rotate(0) translateY(-15%);
        opacity: 0;
        }
    100%{
        -webkit-transform: rotate(0) translateY(0);
                transform: rotate(0) translateY(0);
          opacity: 1;
        }
}
@keyframes fade{
    0%{
        -webkit-transform: rotate(0) translateY(-15%);
                transform: rotate(0) translateY(-15%);
        opacity: 0;
        }
    100%{
        -webkit-transform: rotate(0) translateY(0);
                transform: rotate(0) translateY(0);
          opacity: 1;
        }
}

